			scrollTop: 0,
			title: 'Hello',
			isactive:'',
			kmactive:'',
			xmDetails:['备考导学课','备考导学课',],
			xmIndex:0,
			xmChildactive:0,
			xmList:[
				{
					title:'建筑工程',
					list:[
						{
							content:'一级建造师'
						},
						{
							content:'二级建造师'
						},
					]
				},
				{
					title:'建筑工程1',
					list:[
						{
							content:'一级建造师2'
						},
						{
							content:'二级建造师2'
						},
					]
				},
				{
					title:'消防安全',
					list:[
						{
							content:'一级建造师1'
						},
						{
							content:'二级建造师1'
						},
					]
				},
			],
			        recommendArr:[   // 推荐的商品
					{
					  title:'全部',
